import React, { Fragment } from 'react';
import { generateRandomString } from '../util.js';

const RadioButton = props => {

    let { id, children, dangerouslySetInnerHTML, ...otherProps } = props;

    if ( !id ) {

        id = 'radio-button-' + generateRandomString();
    }

    return  <Fragment>
                <input type="radio" className="rocket__radio" id={ props.id } { ...otherProps } />
                <label className="rocket__label" htmlFor={ props.id }>{ props.label }</label>
            </Fragment>
}

RadioButton.defaultProps = {

    label: ''
}

export {

    RadioButton
}